/* basic  point__cloud styles */
.point__cloud {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  padding: 15px 22px 14px 22px;
  color: var(--black-brown);
  background-color: var(--white);
  box-shadow: 0px 2px 15px rgba(23, 58, 86, 0.1);
  border-radius: 26.5px;
  position: absolute;
  top: 0;
  white-space: nowrap;
  align-self: flex-start;
}

.point__cloud::before {
  position: absolute;
  content: "";
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: var(--white);
  display: block;
}

.point__cloud::after {
  content: "";
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background-color: var(--white);
  position: absolute;
  display: block;
}

/* For point__cloud__left styles */
.point__cloud__left {
  right: 98px;
  margin-top: -46px;
  margin-right: -44px;
}

.point__cloud__left::before {
  bottom: -8px;
  right: 36px;
}

.point__cloud__left::after {
  bottom: -20px;
  right: 26px;
}

.point__cloud__right {
  left: 98px;
  margin-top: -42px;
  margin-left: -40px;
}

.point__cloud__right::before {
  bottom: -8px;
  left: 32px;
}

.point__cloud__right::after {
  bottom: -20px;
  left: 20px;
}

