  display: none;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  transition: all 300ms ease-in;
}

/* reset */

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

/* Vars */
:root {
  --white: #fff;
  --black: #000;
  --brown: #544837;
  --black-brown: #2f281e;
  --accent: #ff9900;
  --accent-darker: #e68a00;
  --darkblue: #173a56;
  --lightblue: #567592;
  --redlight: #ff4e4a;
  --lightgrey: #efefef;

  --text-shadow-default: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

/* Basic rules */
.App {
  min-height: 100vh;
}

.container {
  width: 1110px;
  margin: 0 auto;
}

@media all and (max-width: 1199px) {
  .container {
    width: 960px;
  }
}

@media all and (max-width: 991px) {
  .container {
    width: 750px;
  }
}

body {
  font-family: Poppins, sans-serif;
}

.img__responsive {
  width: 100%;
  height: auto;
  display: block;
}

/* Form */
.error__label {
  font-size: 12px;
  color: #ff4e4a;
  color: var(--redlight);
  display: block;
  margin: 8px 0 10px 0;
}

input[type="text"]:focus,
input[type="email"]:focus {
  outline: 1px solid #ff9900;
  outline: 1px solid var(--accent);
  border-radius: 4px;
}

/* Typography */
.content h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 44px;
  line-height: 54px;
  color: #2f281e;
  color: var(--black-brown);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  text-shadow: var(--text-shadow-default);
}

.content h2 {
  font-size: 36px;
  line-height: 54px;
  color: #2f281e;
  color: var(--black-brown);
  font-weight: bold;
}

.content h3 {
  font-weight: 600;
  font-size: 22px;
  line-height: 33px;
  color: #2f281e;
  color: var(--black-brown);
}

